@import url(https://fonts.googleapis.com/css2?family=Poly:ital@0;1&family=Quando&display=swap);
/* Fontes */

/*
* font-family: 'Poly', serif;
* font-family: 'Quando', serif;
*/

/* Reset de estilos */
*, *::after, *::before {
  box-sizing: border-box;
}

body, h1, h2, h3, ul, li, p {
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  font-size: 1em;
  font-weight: normal;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

/* Grid */
.container {
  width: 960px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.centralizar {
  justify-content: center;
  align-items: center;
  display: flex;
}

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
  margin-left: 10px;
  margin-right: 10px;
}

.col-1 	{width: 60px;}
.col-2 	{width: 140px;}
.col-3 	{width: 220px;}
.col-4 	{width: 300px;}
.col-5 	{width: 380px;}
.col-6 	{width: 460px;}
.col-7 	{width: 540px;}
.col-8 	{width: 620px;}
.col-9 	{width: 700px;}
.col-10 {width: 780px;}
.col-11 {width: 860px;}
.col-12 {width: 940px;}

@media only screen and (max-width: 979px) {
  .container {
      width: 720px;
  }

  .col-1 	{width: 40px;}
  .col-2 	{width: 100px;}
  .col-3 	{width: 160px;}
  .col-4 	{width: 220px;}
  .col-5 	{width: 280px;}
  .col-6 	{width: 340px;}
  .col-7 	{width: 400px;}
  .col-8 	{width: 460px;}
  .col-9 	{width: 520px;}
  .col-10 {width: 580px;}
  .col-11 {width: 640px;}
  .col-12 {width: 700px;}
}

@media only screen and (max-width: 739px) {
  .container {
      width: auto;
      max-width: 360px;
  }

  .col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
      width: 100%;
      margin-bottom: 30px;
      margin-left: 0;
      margin-right: 0;
  }
}
.Header .container-titulo {
  padding: 15px 0;
  background-color: #121F1F;
  font-family: "Quando", serif;
  font-size: 36px;
  text-align: center;
  text-transform: uppercase;
  color: #6AF;
}

.Header .intro {
  padding: 90px 0;
}

.Header .intro p {
  max-width: 460px;
  margin: 0 auto;
  font-family: "Poly", serif;
  font-size: 28px;
  font-style: italic;
  text-align: center;
  line-height: 1.25em;
  color: #FFF;
}

@media only screen and (max-width: 979px) {
  .Header .container-titulo {
    font-size: 28px;
  }

  .Header .intro p {
    font-size: 20px;
    max-width: 300px;
  }
}
footer {
    padding: 30px 0;
    text-align: center;
    background-color: #121F1F;
    box-shadow: 0px -4px 8px rgba(0, 0, 0, 0.4);
    letter-spacing: 1.4px;
}

footer h1 {
    margin: 0 0 15px;
    font-size: 24px;
    font-family: 'Quando', serif;
    color: #28A;
    letter-spacing: 1.5px;
}

footer li {
    margin-top: 15px;
    font-size: 16px;
    font-family: 'Poly', serif;
    color: #F8F8F8;
}

footer li a {
    padding: 8px;
    font-size: 16px;
    font-family: 'Poly', serif;
    text-decoration: none;
    color: #F8F8F8;
    display: inline-block;
}

footer li a:hover {
    color: #28A;
}

footer li > span::after {
    content: "|";
    margin-left: 8px;
    display: inline;
}

@media only screen and (max-width: 979px) {
    footer li {
        margin-top: 30px;
    }

    footer li a {
        padding: 15px 8px;
    }

    footer li > span::after {
        content: "";
        width: 80px;
        height: 2px;
        background-color: currentColor;
        margin: 15px auto 0;
        display: block;
    }
}

@media only screen and (max-width: 739px) {
    footer h1 {
        font-size: 18px;
    }

    footer h1::after {
        margin: 30px auto 15px;
    }

    footer .col-4 {
        width: 50%;
        margin-top: 15px;
        margin-bottom: 15px;
    }
}
.Card {
    margin: 30px auto 0;
    display: flex;
    justify-content: center;
    height: 330px;
}
.Card .img-container {
    border-radius: 4px;
    background-color: #28A;
    display: flex;
    align-items: center;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
    overflow: hidden;
    height: 330px;
}
.Card .img-container img {
    height: 330px;
}
.Card .infos-card {
    height: 330px;
    padding: 30px;
    border-radius: 4px;
    text-align: center;
    background-color: #FFF;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25), inset 8px 0 0 #28A;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.Card .infos-card h2 {
    margin-bottom: 20px;
    font-family: 'Quando', serif;
    font-size: 20px;
    line-height: 1em;
    color: #28A;
}
.Card .infos-card .autor {
    font-family: 'Poly', serif;
    font-size: 18px;
    font-style: italic;
    line-height: 1em;
    color: rgba(18, 31, 31, 0.50);
}
.Card .infos-card .autor::before, .Card .infos-card .autor::after {
    content: "";
    width: 200px;
    height: 1px;
    margin: 10px auto;
    background-color: rgba(18, 31, 31, 0.20);
    display: block;
}
.Card .infos-card p {
    margin-top: 20px;
    font-family: 'Poly', serif;
    font-size: 16px;
    line-height: 1.4em;
}
.Card .infos-card a {
    margin-top: 20px;
    font-family: "Poly", serif;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    line-height: 1em;
    color: #6AF;
    text-decoration: none;
    display: inline-block;
    transition: 0.3s;
}
.Card .infos-card a:hover {
    color: #28A;
}
.Card .infos-card a::after {
    content: "";
    width: 100%;
    height: 1px;
    margin-top: 2px;
    background-color: currentColor;
    display: block;
}
@media only screen and (max-width: 979px) {
    .Card .img-container {
        display: none;
    }
    .Card .infos-card {
        padding-bottom: 15px;
    }
    .Card .infos-card h2 {
        line-height: 1.4em;
    }
    .Card .infos-card a {
        margin-top: 15px;
        padding: 15px 10px;
    }
}
@media only screen and (max-width: 739px) {
    .Card .infos-card {
        margin-bottom: 0;
    }
    .Card .infos-card h2 {
        font-size: 18px;
    }
    .Card .infos-card .autor {
        font-size: 16px;
    }
    .Card .infos-card p {
        font-size: 14px;
    }
}
.Main {
    min-height: 600px;
    padding: 60px 0;
}

.Main h1 {
    font-family: "Quando", serif;
    font-size: 24px;
    color: rgba(18, 31, 31, 0.2);
}

.Main h1::after {
    content: "";
    width: 100%;
    height: 2px;
    margin-top: 6px;
    background-color: currentColor;
    display: block;
}

@-webkit-keyframes carregar {
    from {
        background-color: #6AF;
	}
	to {
        background-color: #28A;
	}
}

@keyframes carregar {
    from {
        background-color: #6AF;
	}
	to {
        background-color: #28A;
	}
}

.Main .box-anima {
    width: 120px;
    margin: 30px auto 0;
    display: flex;
    justify-content: space-evenly;
}

.Main .box-anima .bolinha-anima {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: rgba(102, 170, 255, 0.6);
    -webkit-animation: carregar 0.8s infinite;
            animation: carregar 0.8s infinite;
}



.Main .box-anima > .bolinha-anima:nth-child(2) {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}

.Main .box-anima > .bolinha-anima:nth-child(3) {
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

@media only screen and (max-width: 739px) {
    .Main h1 {
        margin-bottom: 0;
    }
}
